<script>
    export default {
        data() {
            return {
                drawer: null,
                items: [
                    { title: 'Projects', icon: 'build' },
                    { title: 'Blog', icon: 'message' }
                ]
            }
        }
    }
</script>
<template>
    <v-app>
        <v-navigation-drawer v-model="drawer" temporary absolute height='400px'>

            <v-list class="pa-1">
                <v-list-tile avatar>
                    <v-list-tile-avatar>
                        <img src="https://cdn.buttercms.com/lEfaO9dLTOWcieVcTL7a">
                    </v-list-tile-avatar>
                    <v-list-tile-content>
                        <v-list-tile-title>Dan Simonson</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
            </v-list>
            <v-list class="pt-0" dense>
                <v-divider></v-divider>
                <v-list-tile v-for="item in items" :key="item.title">
                    <v-list-tile-action>
                        <v-icon>{{ item.icon }}</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                        <v-list-tile-title>{{ item.title }}</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
            </v-list>
        </v-navigation-drawer>
        <v-toolbar>
            <v-toolbar-side-icon @click.stop="drawer= !drawer"></v-toolbar-side-icon>
            <v-toolbar-title>mariposaweb</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-items class="hidden-sm-and-down">
                <v-btn flat>projects</v-btn>
                <v-btn flat>blog</v-btn>
            </v-toolbar-items>
            <!--
        <v-container>
            <v-layout justify-center>
                <v-btn dark color="pink" @click.stop="drawer = !drawer">Toggle</v-btn>
            </v-layout>
        </v-container>
        -->
        </v-toolbar>
    </v-app>

</template>
<style>
</style>


