<script>
    export default {
        data: () => ({
            icons: ['fab fa-facebook', 'fab fa-twitter', 'fab fa-google-plus', 'fab fa-linkedin', 'fab fa-instagram'],
            rows: [
                {
                    title: 'Company Name',
                    children: ['Here you can use rows and columns here to organize your footer content. Lorem ipsum dolor sit amet, consectetur adipisicing elit']
                },
                {
                    title: 'Products',
                    children: ['MDBootstrap', 'MDWordPress', 'BrandFlow', 'Bootstrap Angular']
                },
                {
                    title: 'Useful Links',
                    children: ['Your account', 'Become an Affiliate', 'Shipping Rates', 'Helper']
                }
            ]
        })
    }
</script>

<template>
    <v-footer height="auto">
        <v-card flat tile class="flex">
            <v-card-title class="teal white--text">
                <strong class="subheading">This site is powered with buttercms and vue. Get connected with us on social networks!</strong>
                <v-spacer></v-spacer>
                <v-btn v-for="icon in icons" :key="icon" icon dark class="mx-3">
                    <v-icon size="24px">{{ icon }}</v-icon>
                </v-btn>
            </v-card-title>

            <v-card-actions class="grey lighten-2 justify-center">
                &copy;2018 —
                <strong>mariposaweb</strong>
            </v-card-actions>
        </v-card>
    </v-footer>
</template>
<style scoped>
</style>


